p {
    color: #383838;
    font-size: 16px;
}
.form-container {
    height: 80%;
    width: 60%;
}

.multifactor-codegen {
    width: 38%;
    background-color: #dfdfdf;
    padding: 15px;
    border-radius: 10px;
}

#nameDiv, #passwordDiv {
    width: 90%;
    padding: 15px;
    margin: 0 auto;
    background-color: #dfdfdf;
    border-radius: 10px;
}

#nameDiv {
    height: 100px;
    display: flex;
    align-items: center;
    align-content: center;

}

#nameDiv div {
    width: 60%;
}

#passwordDiv {
    height: auto;
    margin-top: 15px;
}

